/*===========================================
================ CHUNK: BASE/RTYPOGRAPHY
===========================================*/
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto/Roboto-Regular.ttf");
}
html {
  font-size: min(1rem + 0.1764705882vw, 1.35rem);
  font-family: "Roboto", sans-serif;
  color: #444;
}

h1, h2, h3, h4, h5 {
  margin-bottom: 5px;
}

p {
  color: #888;
}

/*===========================================
================ CHUNK: BASE/RESET
===========================================*/
* {
  box-sizing: border-box;
  margin: 0;
}

body {
  scroll-behavior: smooth;
  min-height: 100vh;
}

button, input, select, textarea {
  width: 190px;
}

button {
  border: none;
  cursor: pointer;
}

p {
  max-width: 550px;
}

a,
a:active,
a:hover,
a:visited {
  text-decoration: none;
  color: #e0a502;
}

ul {
  list-style: none;
  padding: 0;
}

/*===========================================
================ CHUNK: BASE/LAYOUT
===========================================*/
.main-grid {
  min-height: 100vh;
  display: grid;
  grid: "nav header" auto "nav main  " 1fr/auto 1fr;
}
.main-grid > * {
  padding: 20px;
}

nav {
  grid-area: nav;
  background: #e0a502;
  color: #FFFFFF;
  display: grid;
  align-content: start;
  grid-template-columns: max-content;
  gap: 10vh;
}
nav h3 {
  display: inline;
}
nav .icon {
  margin-right: 10px;
}
nav li {
  cursor: pointer;
}
nav ul li + li {
  margin-top: 20px;
}

header {
  grid-area: header;
}
header > div:first-child {
  margin-bottom: 30px;
}
header .upper {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-flow: column;
  align-items: center;
  gap: 20px;
}
header .upper input {
  width: calc(100% - 50px);
}
header .profile-pic {
  width: 30px;
  height: 30px;
  background-size: cover;
  background-position: center;
  background-image: url("../media/snake.png");
}
header .lower {
  display: grid;
  justify-content: space-between;
  grid-auto-flow: column;
  align-items: center;
}
header .lower .profile-info {
  display: grid;
  grid-template: "i hi      " 1fr "i username" 1fr/30px 1fr;
  column-gap: 15px;
  align-items: center;
}
header .lower .profile-info .profile-pic {
  grid-area: i;
}
header .lower .profile-info .hi-there {
  grid-area: hi;
  font-size: 0.8rem;
}
header .lower .profile-info .profile-name {
  grid-area: username;
}
header button {
  border-radius: 15px;
  background: rgb(32, 166, 219);
  color: white;
  line-height: 30px;
  width: 100px;
}
header button + button {
  margin-left: 20px;
}

.main {
  background-color: rgba(247, 221, 166, 0.26);
  grid-area: main;
  display: grid;
  grid-template-columns: 1fr 300px;
  align-items: start;
  gap: 20px;
}
.main .card {
  background: white;
  box-shadow: 0 0 5px rgba(200, 200, 200, 0.5);
  border-radius: 8px;
  padding: 20px;
}
.main h3 {
  margin-bottom: 20px;
}
.main .projects h2 {
  margin-bottom: 20px;
}
.main .projects .cards {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
}
.main .projects .cards .card {
  position: relative;
  overflow: hidden;
  padding: 20px;
  background: white;
}
.main .projects .cards .card .actions {
  margin-top: 20px;
  text-align: right;
}
.main .projects .cards .card button {
  width: 30px;
  background: none;
  color: rgb(32, 166, 219);
}
.main .projects .cards .card button + button {
  margin-left: 10px;
}
.main .projects .cards .card ::before {
  content: "";
  height: 100%;
  width: 6px;
  position: absolute;
  top: 0;
  left: 0;
  background: rgb(32, 166, 219);
}
.main aside h2 {
  margin-bottom: 20px;
}
.main aside .cards {
  display: grid;
  gap: 30px;
}
.main aside .cards div:last-of-type .card > div {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: auto 1fr;
  font-size: 0.85rem;
}
.main aside .cards div:last-of-type .card > div + div {
  margin-top: 20px;
}
.main aside .cards div:last-of-type .card > div > .icon {
  width: 40px;
  height: 40px;
  background-size: cover;
  background-position: center;
  margin-right: 10px;
}
.main aside .cards div:last-of-type .card > div:nth-of-type(1) > .icon {
  background-image: url("../media/snake.png");
}
.main aside .cards div:last-of-type .card > div:nth-of-type(2) > .icon {
  background-image: url("../media/vr.png");
}
.main aside .cards div:last-of-type .card > div:nth-of-type(3) > .icon {
  background-image: url("../media/crocodile.png");
}
.main aside .cards div:last-of-type .card > div:nth-of-type(4) > .icon {
  background-image: url("../media/dog.png");
}
.main aside hr {
  margin: 15px auto;
  border: none;
  background: #EEE;
  height: 2px;
}

input {
  border: 1px solid transparent;
  border-radius: 15px;
  background-color: rgba(247, 221, 166, 0.26);
  max-width: 600px;
}
input:focus {
  border: 1px solid #e0a502;
  outline: none;
}